
@import "./reset.scss";
@import "./vars.scss";
@import "./grid.scss";
@import "./general.scss";

.intro {
  height: 100vh;
  display: flex;
  align-items: center;
  background-image: url("../img/intro.png");
  background-size: cover;
  background-position: center;
  position: relative;
  z-index: 0;
  color: #fff;
  text-align: center;
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    background-color: #555555;
    opacity: 0.6;
    mix-blend-mode: multiply;
  }
  &__logo {
    position: absolute;
    
  }
  &__title {
    font-size: 7.2rem;
    font-weight: 700;
    font-family: $font-title;
    letter-spacing: 0.5rem;
  }
  &__subtitle {
    font-size: 3rem;
    text-transform: uppercase;
    margin: 3rem 0 9rem;
  }
  &__form {
    // @include col-lg(8);
    background-color: #fff;
  }
}

.destinations {
  padding: 10rem 0;
  &__container {
    margin-top: 8.5rem;
  }
  &__item {
    @include col-lg(4);
    margin-bottom: $gutter * 2;
    &--wide {
      @include col-lg(8);
    }
  }
}

.destination {
  height: 29rem;
  @media (min-width: $xl) {
    height: 37rem;
  }
  background-image: url("../img/service1.png");
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 3rem;
  position: relative;
  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 50%;
    opacity: 0.6;
    mix-blend-mode: multiply;
    background: linear-gradient(#fff, #000);
  }
}

.section-title {
  text-align: center;
}
